\n        font-family: \"Microsoft YaHei\";\r\n        font-size: 14px;\r\n        display: inline-block;\r\n        padding: 5px 6px;\r\n        box-sizing: border-box;\r\n        color: #333333;\r\n        line-height: 22px;\r\n\r\n    }\r\n</style>\r\n"],"sourceRoot":""}]);

